import React, { useEffect, useState } from 'react';
import {
  Box,
  Grid,
  Container,
  IconButton,
  Drawer,
  List,
  ListItemButton,
  ListItemText,
  Typography,
  AppBar,
  Toolbar
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';

import FiltersPanel from '../components/FiltersPanel';
import SummaryCards from '../components/SummaryCards';
import ChartsPanel from '../components/ChartsPanel';
import ItemsTable from '../components/ItemsTable';
import Header from '../components/Header';

import { fetchItems, fetchSummary, fetchCharts } from '../utils/api';
import { useDashboard } from '../context/DashboardContext';

export default function DashboardPage() {
  const { filters, setFilters } = useDashboard();
  const [data, setData] = useState({ items: [], total: 0, page: 1, pageSize: 10 });
  const [summary, setSummary] = useState([]);
  const [chartsData, setChartsData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [drawerOpen, setDrawerOpen] = useState(false);

  // Sidebar menu
  const menuItems = [
    { label: 'Dashboard', onClick: () => console.log('Dashboard clicked') },
    { label: 'Reports', onClick: () => console.log('Reports clicked') },
    { label: 'Settings', onClick: () => console.log('Settings clicked') },
  ];

  // Load table data
  const loadTable = async () => {
    setLoading(true);
    try {
      const res = await fetchItems(filters);
      setData({ items: res.items, total: res.total, page: res.page, pageSize: res.pageSize });
    } catch (err) {
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadTable();
  }, [
    filters.page,
    filters.pageSize,
    filters.search,
    filters.category,
    filters.status,
    filters.startDate,
    filters.endDate,
    filters.sortBy,
    filters.sortDir
  ]);

  useEffect(() => {
    fetchSummary().then(setSummary).catch(console.error);
    fetchCharts().then(setChartsData).catch(console.error);
  }, []);

  const sidebar = (
    <Box sx={{ width: 240, bgcolor: '#1e1e2f', color: 'white', height: '100%' }}>
      <Typography variant="h6" sx={{ p: 2 }}>Menu</Typography>
      <List>
        {menuItems.map((item, idx) => (
          <ListItemButton key={idx} onClick={item.onClick}>
            <ListItemText primary={item.label} />
          </ListItemButton>
        ))}
      </List>
    </Box>
  );

  return (
    <Box sx={{ display: 'flex', minHeight: '100vh', bgcolor: '#f7f8fa' }}>
      {/* AppBar for mobile */}
      <AppBar position="fixed" sx={{ display: { md: 'none' } }}>
        <Toolbar>
          <IconButton edge="start" color="inherit" onClick={() => setDrawerOpen(true)}>
            <MenuIcon />
          </IconButton>
          <Typography variant="h6">Dashboard</Typography>
        </Toolbar>
      </AppBar>

      {/* Desktop Sidebar */}
      <Box
        sx={{
          display: { xs: 'none', md: 'block' },
          width: 240,
          flexShrink: 0,
          position: 'fixed',
          top: 0,
          bottom: 0,
        }}
      >
        {sidebar}
      </Box>

      {/* Drawer for mobile */}
      <Drawer
        open={drawerOpen}
        onClose={() => setDrawerOpen(false)}
        sx={{ display: { xs: 'block', md: 'none' } }}
      >
        {sidebar}
      </Drawer>

      {/* Main Content */}
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          ml: { md: '240px' },
          pt: { xs: 8, md: 2 },
          px: 2,
        }}
      >
        <Header />

        <Container maxWidth="xl" sx={{ mt: 2, mb: 4 }}>
          <SummaryCards metrics={summary} />

          <Box sx={{ mt: 4, mb: 4 }}>
            <ChartsPanel items={chartsData} />
          </Box>

          <Grid container spacing={2} sx={{ mt: 2 }}>
            {/* Filters Panel */}
            <Grid item xs={12} md={3}>
              <FiltersPanel onFilterChange={() => setFilters(prev => ({ ...prev, page: 1 }))} />
            </Grid>

            {/* Items Table */}
            <Grid item xs={12} md={9}>
              <ItemsTable
                items={data.items}
                total={data.total}
                page={data.page}
                pageSize={data.pageSize}
                loading={loading}
                onPageChange={page => setFilters(prev => ({ ...prev, page }))}
                onPageSizeChange={pageSize => setFilters(prev => ({ ...prev, pageSize, page: 1 }))}
                onSortChange={(sortBy, sortDir) => setFilters(prev => ({ ...prev, sortBy, sortDir }))}
              />
            </Grid>
          </Grid>
        </Container>
      </Box>
    </Box>
  );
}
